@import url('https://fonts.googleapis.com/css?family=Roboto+Slab');
body{
	background-color: #eee;
}
header{
	height: 300px;
	width: 100%;
	background-color: #17313b;
}
content{
	height: auto;
	min-height: 200px;
}
footer{
	height: 400px;
	background-color: #cacaca;
	margin-top: 80px;
}
.display-none{
	display: none;
}
button{
		color: #fff;
		height: 26px;
		background-color: #17313b;
		border: none;
}
.header-navbar{
	padding-top: 25px;
	color: #fff;
	position: absolute;
	left: 0;
	top: 100px;
	width: 100%;
	height: 65px;
}
.header-navbar ul li{
	float: left;
	list-style-type: none;
	margin-right: 12px;
	font-size: 16px;
}
.header-navbar ul li a:hover{
	color: #fff;
	text-decoration: none;
}
.loginButton{
	color: #fff;
	float: right;
	margin: 18px;
	margin-right: 32px;
	position: relative;
	width: 250px;
	height: auto;
	padding: 8px;
	height: auto;
	overflow: hidden;
}
.loginButton div{
	float: left;
	clear: right;
}
.loginButton ul{
	list-style-type: none;
	margin: 0px;
	padding: 0px;
	position: relative;
	display: table;
	opacity: 0;
	clear:left;	
}
.loginButton ul:hover{
	display: table;
}
.loginButton ul li{
	height: 24px;
}
.loginButton:hover ul{
	opacity: 1;

}
.header{
	padding-top: 50px;
}
.header-navbar a{
	color: #fff;
}
.header-navbar a:hover{
	color: #000;
}
.search{
	width: 700px;
	outline: none;
}
.search-button{
	background-color: #17313b;
	border: none;
	height: 26px;
	width: 80px;
	color: #fff;
	outline: none;
}
.search-block{
}
.search-block div{
	margin-top: 6px;
}
.menu{
	background-color: #e9e9e9; 
	padding: 10px;
	font-size: 12px;

}
.filter li{
	list-style-type: none;
}
.sort div{
	float: left;
	margin: 5px;
}
.purchase{
	border-top: solid 1px black;
	height: 160px;
}
.purchase-head{
	color: #248dd4;
}
.purchase-price{
	float: right;
}
.purchase-date{
	float: right;
}
.purchase-button-auction{
	color: #3781b3;
	width: 80%;
	float: left;
}
.purchase-text{
	width: 80%;
}
.search-status{
	height: 60px;
	padding: 10px;
}
.content{
	margin-left: 10px;
}
.logIn a{
	float: right;
	color: #fff;
	margin: 10px;
}
.avatar{
	width: 100px;
	height: 100px;
	border: solid 1px black;
	border-radius: 50px;
	float: left;
}
.personData{
	margin: 16px;
}
.personFIO{
	font-size: 20px;
	height: 100px;
}
.personInfo{
	
	padding: 28px;
}
.block{
	display:block;
}

.search-filter div{
	float: left;
	margin: 10px;
}
.document{
	background-color: #f0f0f0;
	margin: 16px;
	padding: 16px;
	height: 140px;
}
.document:hover{
	background-color: #e0e0e0;
}
.document-head{
	font-size: 20px;
	margin-bottom: 16px;
	font-family: serif;
}
.document-nomination{
	color: #a0a0a0;
	margin-bottom: 8px;
}
.add-document{
	text-align: center;
}
.add-document-button{
	border: solid black 1px;
	border-radius: 50px;
	width: 70px;
	height: 70px;
	margin: auto;
	margin-bottom: 16px;
}
.dropdown-procurment .dropdown-procurment-paragraph{
	background-color: #f0f0f0;
	margin: 16px;
	height: 32px;
	width: 400px;
	font-size: 20px;
}
.dropdown-procurment .dropdown-procurment-paragraph:hover{
	background-color: #e0e0e0;
}
.purchase-person-head{
	font-size: 32px;
}
.purchase-person-price{
	font-size: 23px;
	margin-bottom: 20px;
}
.purchase-person-town{
	margin-bottom: 20px;
}
.purchase-person-button{
	margin-bottom: 12px;
}

.content{	
	margin: auto;
	width: 1200px;
}
.purchase-info-card{
	margin: auto;
	width: 900px;
	margin-bottom: 24px;
	border: 1px solid #d9d9d9;
	border-bottom: 0px;
}
.purchase-info-card-head{
	color: #fff;
	background-color: #17313b;
	min-height: 38px;
	height: auto;
	font-size: 22px;
	padding-left: 12px;
	padding-top: 6px;
	border-bottom:  solid 1px #050b0d;
	font-family: 'Roboto Slab', serif;
}
.purchase-info-card-line{
	background-color: #f5f5f5; 
	min-height: 36px;
	height: auto;
	font-size: 14px;
	padding: 12px;
	margin: 6px;
	border-bottom: solid 1px #d9d9d9;
	margin: 6px;
	overflow: hidden;
	font-family: 'Roboto Slab', serif;
}
.card-info-name{
	float: left;
	width: 30%;
	font-weight: bold
	
}
.card-info{
	float: left;
	width: 70%;

}
.purchase-page-head{
	margin: auto;
	width: 900px;
	margin-bottom: 22px;
	margin-top: 22px;
	font-size: 28px;
	font-weight: thin;
	color: #555;

}
.purchase-page-navbar{
	width: 900px;
	height: 50px;
	background-color: #17313b;
	color: #fff;
	margin: auto;
	margin-top: 32px;
	margin-bottom: 32px;
}
.purchase-page-navbar a{
	float: left;
	width: 33.33333%;
	height: inherit;
	text-align: center;
	padding-bottom: 12px;
	padding-top: 5px;
	font-size: 22px;
	color: #fff;
}
.purchase-page-navbar a:hover{
	float: left;
	width: 33.33333%;
	height: inherit;
	padding-bottom: 12px;
	padding-top: 5px;
	color: #fff;
	text-decoration: none;
	border-bottom: 2px solid #f76969;
}
.purchase-page-navbar a:focus{
	text-decoration: none;
}
.purchase-page-navbar-active{
	border-bottom: 5px solid #f76969;
}
.purchase-page-navbar-active:hover{
	border-bottom: 7px solid #f76969;
}
@media (max-width: 450px){
	.search{
	width: 100%;
	outline: none;
}
		.content{	
		width: 100%;
		padding: none;
	}
	.header{
		display: none;
	}
	.purchase-info-card{
	margin: auto;
	width: 100%;
	margin-bottom: 24px;
	border: 1px solid #d9d9d9;
	border-bottom: 0px;
	padding: none;
	border: none;

}
.purchase-info-card-head{
	font-size: 14px;
	padding: 6px;
}
.purchase-info-card-line{
	margin: none;
	font-size: 8px;
	padding: 6px;
	
}
.card-info-name{
	float: none;
	width: 30%;
	font-weight: bold
	
}
.card-info{
	float: none;
	width: 70%;
	margin-top: 8px;

}
.purchase-page-head{
	width: 100%;
}
.purchase-page-navbar{
	width: 100%;
}
.purchase-page-navbar a{
	font-size: 13px;
	padding: 6px;
	text-align: left;
}
.purchase-page-navbar a{
	font-size: 13px;
	padding: 6px;
	text-align: left;
}
.purchase-page-navbar-active{
	font-size: 13px;
}
}



